(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('AdminSkillModalController', AdminSkillModalController);

	/** @ngInject */
	function AdminSkillModalController(toastrService, modalService, adminService, HelperService) {
		var vm = this;
		vm.skill = modalService.getResult();

		vm.saveSkill = function(skill) {
			if (skillIsInvalid(skill)) {
				return toastrService.showToastr(400);
			}
      adminService.saveSkill(skill).then(function(status) {
				toastrService.showToastr(status);
				modalService.okModal();
			});
    }

		vm.closeModal = function() {
			modalService.closeModal();
		}

		function skillIsInvalid(skill) {
			return HelperService.isUndefinedOrNull(skill)
				|| HelperService.isUndefinedOrNull(skill.name) || skill.name === ""
				|| HelperService.isUndefinedOrNull(skill.description) || skill.description === "";
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('SceneModalController', SceneModalController);

	/** @ngInject */
	function SceneModalController($stateParams, toastrService, modalService, adminService, HelperService) {
		var vm = this;
		vm.scene = modalService.getResult();
		vm.question = {alternatives: new Array(4)};
		vm.allSkills = [];
		vm.skillSelected = null;
		var themeId = $stateParams.themeId;

		init();

		function init() {
			loadSkills();
			if (vm.scene && vm.scene.question) {
				vm.question = vm.scene.question;
			}
		}

		vm.saveScene = function(scene) {
			if (sceneIsInvalid(scene)) {
				return toastrService.showToastr(400);
			}
			scene.gameThemeId = themeId;
			adminService.saveScene(scene).then(function(status) {
				toastrService.showToastr(status);
				modalService.okModal();
			});
		}

		vm.saveSceneWithQuestion = function(scene, question) {
			if (!questionIsValid(question)) {
				toastrService.showToastr(400);
				return;
			}
			question.skillId = vm.skillSelected.id;
			scene.question = question;
			vm.saveScene(scene);
		}

		/**
		* Carrega todas competencias cadastadas
		*/
		function loadSkills() {
			adminService.loadAllSkills().then(function(data) {
				vm.allSkills = data;
				loadSkillSelected();
			});
		}

		/**
		* Carrega a selecao da competencia no select
		*/
		function loadSkillSelected() {
			if(HelperService.isUndefinedOrNull(vm.scene) || HelperService.isUndefinedOrNull(vm.scene.question)) {
				return;
			}
			var skillId = vm.scene.question.skillId;
			angular.forEach(vm.allSkills, function(skill) {
				if (skill.id == skillId) {
					vm.skillSelected = skill;
				}
			});
		}

		function questionIsValid(question) {
			if (HelperService.isUndefinedOrNull(vm.skillSelected)) {
				return false;
			}
			for (var index = 0; index < 4; index++) {
				var alternative = question.alternatives[index];
				if (HelperService.isUndefinedOrNull(alternative)
						|| HelperService.isUndefinedOrNull(alternative.description)
						|| HelperService.isUndefinedOrNull(alternative.skillValue)) {
					return false;
				}
			}
			return true;
		}

		function sceneIsInvalid(scene) {
			return HelperService.isUndefinedOrNull(scene)
				|| HelperService.isUndefinedOrNull(scene.text)
				|| scene.text == false
				|| HelperService.isUndefinedOrNull(scene.background)
		}

		vm.closeModal = function() {
			modalService.closeModal();
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('InstitutionModalController', InstitutionModalController);

	/** @ngInject */
	function InstitutionModalController(modalService, HelperService, toastrService, loginService) {
		var vm = this;
		vm.institution = modalService.getResult();
		vm.allLevels = ["TECHNICAL", "SUPERIOR"];
		vm.checkboxPassword = false;
		vm.showPassword = {"true" : "text", "false" : "password"};

		vm.saveInstitution = function(institution) {
			if(validateInstitution(institution)) {
				toastrService.showToastr(400);
				return;
			}
			modalService.okModal(institution);
		}

		vm.updatePassword = function(user) {
      loginService.updatePassword(user.username, user.password).then(function(status) {
        toastrService.showToastr(status);
      });
    }

		vm.closeModal = function() {
			modalService.closeModal();
		}

		function validateInstitution(institution) {
			return (HelperService.isUndefinedOrNull(institution) || HelperService.isUndefinedOrNull(institution.code) ||
							HelperService.isUndefinedOrNull(institution.cnpj) || HelperService.isUndefinedOrNull(institution.company) ||
							HelperService.isUndefinedOrNull(institution.level) || HelperService.isUndefinedOrNull(institution.city) ||
							HelperService.isUndefinedOrNull(institution.mentors) || HelperService.isUndefinedOrNull(institution.mentors[0].name) ||
							HelperService.isUndefinedOrNull(institution.mentors[0].username));
		}
	}
})();

