(function() {
  'use strict';

  angular
    .module('mapskillsWeb')
    .directive('acmeSidebar', acmeSidebar);

  /** @ngInject */
  function acmeSidebar() {

    var directive = {
      restrict: 'E',
      templateUrl: function(element, attrs) {
        return "app/shared/sidebar/" + attrs.profile + ".html";
      },
      controller: SidebarController,
      controllerAs: 'sbCtrl',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function SidebarController(moment, $location, $state, $stateParams, storageService) {
      var vm = this;
      vm.isCollapsed = true;
      vm.changeClass = changeClass;
      vm.goPage = goPage;
      vm.ngClass = {dashboard : "", institutions : "",
                    themes : "", skills : "",
                    students : "", report : ""};

    init();
    /**
     * função para ativação do class dos botões de navegação.
     */
     function changeClass(page) {
       page = page.indexOf('dashboard') == 0 ? 'dashboard' : page;
       var lastPage = storageService.getItem('page') || null;
       storageService.setItem('page', page);
       if (lastPage) {
         vm.ngClass[lastPage] = "null";
       }
       vm.ngClass[page] = "active";
       goPage(page, null);
     }

     function goPage(page, params) {
       $state.go("^." + page, params);
     }

     function init() {
       var url = $location.path();
       if (url.indexOf("/admin/theme/") !== -1) {
         vm.ngClass["themes"] = "active";
         return $state.go("admin.scenes", $stateParams);
       }
       var position = $location.path().lastIndexOf("/");
       vm.changeClass(url.substring(position + 1));
     }

    }
  }

})();

(function() {
  'use strict';

  angular
    .module('mapskillsWeb')
    .factory('Session', Session);

  /** @ngInject */
  function Session($cookies, application, $rootScope) {
    var createToken = function(tokenInfo) {
        var sessionObj = {
            token: tokenInfo
        };
        $cookies.put(application.authentication.mapskillsCookieKey, angular.toJson(sessionObj));
    };

    var createUser = function(userInfo) {
      var json = $cookies.get(application.authentication.mapskillsCookieKey);
      var sessionObj = angular.fromJson(json);
      sessionObj.user = userInfo;
      $cookies.put(application.authentication.mapskillsCookieKey, angular.toJson(sessionObj));
      this.user = userInfo;
      $rootScope.$broadcast('user:updated', this.user);
    }

    var destroy = function() {
        $cookies.remove(application.authentication.mapskillsCookieKey);
        this.user = null;
    };

    var hasSession = function() {
        return ($cookies.get(application.authentication.mapskillsCookieKey));
    };

    var refreshUserSession = function() {
        if (hasSession()) {
            var json = $cookies.get(application.authentication.mapskillsCookieKey);
            var obj = angular.fromJson(json);
            return obj.user;
        }
        return null;
    };

    var token = function() {
        if (hasSession()) {
            var json = $cookies.get(application.authentication.mapskillsCookieKey);
            var obj = angular.fromJson(json);
            return obj.token;
        }
        return null;
    };

    var userPrivileges = function() {
        if (hasSession()) {
            var json = $cookies.get(application.authentication.mapskillsCookieKey);
            var obj = angular.fromJson(json);
            return obj.user.privileges;
        }
    };
    var service = {
      createToken: createToken,
      createUser: createUser,
      destroy: destroy,
      hasSession: hasSession,
      refreshUserSession: refreshUserSession,
      token: token,
      userPrivileges: userPrivileges
    };
    return service;
  }
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.factory('reportService', reportService);

		/** @ngInject */
		function reportService($http, $q, $log, HelperService, downloadService, API_SERVER) {
			var studentsPageCached = {students: [], remainingPages: 0};

			return {
				search : _search,
        download : _download
			};

      function _search(filter, clearCache, page) {
        var deferred = $q.defer();
				if (clearCache) {
					studentsPageCached.students = [];
					studentsPageCached.remainingPages = 0;
				}
				if (page.isLast) {
					deferred.resolve(studentsPageCached);
				} else {
					var uri = API_SERVER.REPORT.SHOW;
					$http.get(uri, {params: filter}).then(function success(response) {
						studentsPageCached.students = studentsPageCached.students.concat(response.data.students);
						studentsPageCached.remainingPages = response.data.remainingPages;
						deferred.resolve(studentsPageCached);
					}, function error(response) {
						$log.error(response);
						deferred.reject(response);
					});
				}

        return deferred.promise;
      }

      function _download(filter) {
        downloadService.download(filter);
      }

		}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('ReportController', ReportController);

	/** @ngInject */
	function ReportController(adminService, Session, HelperService, reportService, toastr, $uibModal) {
		var vm = this;
		var page = {nextPage: 0, size: 40, isLast: false};
		vm.template = 'app/shared/report/template/card.view.html';
    vm.isAdmin = true;
    vm.filter = {institutionLevel: null, institutionCode: null, courseCode: null, startDate: null, endDate: null};
    vm.institutions = [];
    vm.institutionSelected;
    vm.courseSelected;

    vm.report = {};

		function init() {
			var userLogged = Session.refreshUserSession();
			vm.isAdmin = isAdmin(userLogged);
			if (vm.isAdmin) {
				adminService.loadAllInstitutions(true).then(function(response) {
					angular.copy(response, vm.institutions);
				});
			} else {
				vm.getInstitutionCourses(userLogged.institution.id);
			}
		}
		/**
		 * Chama do serviço de download com condicionais do filtro.
		 */
    vm.download = function() {
			reportService.download(getFilter());
    }
		/**
		 * Realiza chamada para busca do relatório.
		 */
    vm.search = function(clearCache) {
			if (clearCache) {
				page.nextPage = 0;
				page.isLast = false;
			}
			reportService.search(getFilter(), clearCache, page).then(function success(response) {
				page.nextPage++;
				page.isLast = response.remainingPages <= 0;
				vm.students = angular.copy(response.students);
			}, function error() {
				toastr.error("Problema ao tentar buscar relatório.", ":\\");
			});
    }

		vm.getInstitutionCourses = function(id) {
			if (angular.isUndefined(id)) {
				return;
			}
			adminService.getInstitutionDetails(id).then(function(response) {
				vm.institutionSelected = angular.copy(response);
			});
		}

		vm.loadMoreStudents = function() {
			vm.search(false);
		}

		/**
		 * Responsavel por manipular o modal com os resultados do aluno selecionado.
		 */
		vm.studentResult = function(student) {
			$uibModal.open({
				animation: true,
				templateUrl: 'app/shared/report/modal/report.modal.html',
				controllerAs: 'vm',
				controller: function($uibModalInstance) {
					var vm = this;
					vm.skills = [];
					vm.data = [];
					vm.series = ['Valor minímo', 'Valor máximo', 'Valor do aluno'];
					vm.student = student;
					vm.options = {
						legend: {	display: true }
					};

					(function init() {
						var values = [];
						var minimumValues = [];
						var maxValues = [];
						angular.forEach(vm.student.skillResult, function(skill) {
							minimumValues.push(0);
							maxValues.push(20);
							vm.skills.push(skill.skill);
							values.push(skill.value);
						});
						vm.data.push(minimumValues);
						vm.data.push(maxValues);
						vm.data.push(values);
					})();

					vm.close = function () {
						$uibModalInstance.dismiss('cancel');
					}
				},
				size: 'md'
			});
		}
    /**
		 * Retorna filtro para realização da pesquisa ou download do csv
		 */
    var getFilter = function() {
			var filter = {
				institutionLevel: vm.institutionSelected == null ? null : angular.copy(vm.institutionSelected.level),
				institutionCode: vm.institutionSelected == null ? null : angular.copy(vm.institutionSelected.code),
				courseCode: vm.courseSelected == null ? null : angular.copy(vm.courseSelected.code),
				startYear: getYear(vm.filter.startDate),
				startSemester: getSemester(vm.filter.startDate),
				endYear: getYear(vm.filter.endDate),
				endSemester: getSemester(vm.filter.endDate),
				page: page.nextPage,
				size: page.size
			};

			return filter;
    }

		var isAdmin = function(user) {
			return user.profile === "ADMINISTRATOR";
		}

		var getYear = function(filterDate) {
			return filterDate == null || filterDate.length < 5 ? null : filterDate.substring(0, 4);
		}

		var getSemester = function(filterDate) {
			return filterDate == null || filterDate.length < 5 ? null : filterDate.substring(4);
		}

		init();

	}

})();

